/* ------------------------------------------------
	gnav
------------------------------------------------ */
@media only screen and (min-width: 1025px) {
  .link-gnav {
    position: relative;
  }
  .link-gnav:after {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    content: "";
    width: 22px;
    height: 22px;
    margin: -12px 0 0 -12px;
    border-radius: 50%;
    border: 1px solid #cfcfcf;
    transition: all 0.1s;
    opacity: 0;
  }
  .link-gnav:hover {
    cursor: pointer;
  }
  .link-gnav:hover:after {
    animation: circle 0.5s;
    animation-fill-mode: both;
  }
  @keyframes circle {
    0% {
      transform: scale(0.1);
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: scale(2);
      opacity: 0;
      display: none;
    }
  }
}
