/* ------------------------------------------------
	CheckBox
------------------------------------------------ */
.nav-inputbox {
  /* disabled state */
  /* 
  The animation of switching states
  */
}
.nav-inputbox .check-box {
  position: relative;
}
.nav-inputbox .check-input {
  opacity: 0;
}
.nav-inputbox .check-input:not(:disabled) ~ .check-label {
  cursor: pointer;
}
.nav-inputbox .check-text {
  position: absolute;
  left: 0;
}
.nav-inputbox .check-label::after {
  content: "";
  box-sizing: border-box;
  width: 1em;
  height: 1em;
  font-size: 1.25rem;
  /* 1 */
  background-color: transparent;
  border: 1px solid #222;
  position: absolute;
  right: 0;
  top: 2px;
  z-index: 2;
}
.nav-inputbox .check-input:checked ~ .check-label::after {
  background-color: #222;
}
.nav-inputbox .check-label::before {
  content: "";
  width: 0;
  height: 0;
  font-size: 1.25rem;
  /* 1 */
  border-left-width: 0;
  border-bottom-width: 0;
  border-left-style: solid;
  border-bottom-style: solid;
  border-color: #fff;
  position: absolute;
  top: 0.6428em;
  right: 0.28em;
  z-index: 3;
  transform-origin: left top;
  transform: rotate(-40deg) skew(10deg);
}
.nav-inputbox .check-input:checked ~ .check-label::before {
  width: 6px;
  height: 4px;
  border-left-width: var(--uiToggleArrowWidth);
  border-bottom-width: 2px;
}
.nav-inputbox .check-input:disabled ~ .check-label {
  opacity: var(--toggleOpacityDisabled, 0.24);
  cursor: var(--toggleCursorDisabled, not-allowed);
  user-select: none;
}
.nav-inputbox .check-input:not(:disabled) ~ .check-label::before {
  will-change: width, height;
  opacity: 0;
}
.nav-inputbox .check-input:not(:disabled):checked ~ .check-label::before {
  opacity: 1;
  transition: opacity 0.1s ease-out 0.25s;
}
.nav-inputbox .check-input:not(:disabled) ~ .check-label::after {
  will-change: background-color;
  transition: background-color 0.2s ease-out;
}

.gnav-inputbox {
  /* disabled state */
  /* 
  The animation of switching states
  */
}
.gnav-inputbox .check-box {
  width: 100%;
  height: 100%;
}
.gnav-inputbox .check-input {
  opacity: 0;
  display: none;
}
.gnav-inputbox .check-label {
