/*////////////////////////////////////////////////////

	汎用レイアウト（レイアウトセット参照）
		ここではレイアウトに関わるスタイルのみ記載し、
		背景色や背景画像、ボーダー、シャドウなどのデザインバリエーションは、
		_pattern.scss内に継承クラスで指定すること
		（例：.wrapper01.style01）
		l-header
		l-header-inner 内側の枠
		l-header-outer　外側の枠

		l-frame > l-container > l-contents > l-box

		l-frame コンテンツ部分の大枠　ページタイトルとコンテンツの区切り
		l-container　コンテンツの内枠
		l-contents　H1見出し区切り

////////////////////////////////////////////////////*/
/* ------------------------------------------------
	Grid レイアウト
	gridレイアウトに関わる記述のみ書くこと
------------------------------------------------ */
.l-grid {
  grid-template-columns: 100%;
  grid-template-rows: auto;
}
@media only screen and (min-width: 1025px) {
  .l-grid {
    display: grid;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .l-grid {
    display: grid;
  }
}
@media only screen and (max-width: 767px) {
  .l-grid {
    grid-template-columns: auto;
  }
}
.l-grid header {
  grid-column: 1/2;
  grid-row: 1/2;
}
.l-grid header .l-header {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  grid-template-columns: 1fr var(--nav-lang-width) var(--nav-gnav-width);
}
@media only screen and (max-width: 767px) {
  .l-grid header .l-header {
    grid-template-columns: 1fr var(--nav-lang-width) var(--nav-gnav-width);
  }
}
.l-grid main {
  grid-column: 1/2;
  grid-row: 2/3;
}
.l-grid main .l-frame {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto;
}
.l-grid footer {
  grid-column: 1/2;
  grid-row: 3/4;
  background: var(--bg-color03);
}

