/*////////////////////////////////////////////////////

	状態の変化があるcssを書く

	is-○○　系統

////////////////////////////////////////////////////*/
.is-view {
  overflow-y: hidden;
}
.is-view .nav-inner {
  visibility: visible;
  opacity: 1;
  z-index: 998;
}
.is-view .l-gnav {
  visibility: visible;
  opacity: 1;
}
.is-view .l-gnav .link-gnav:before {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  content: "";
  width: 43px;
  height: 43px;
  margin: -23px 0 0 -23px;
  border-radius: 50%;
  opacity: 0;
  border: 1px solid rgba(255, 255, 255, 0.3);
  animation: is-circle 0.8s;
  animation-fill-mode: both;
}
@keyframes is-circle {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.is-view .l-gnav .gnav-botton {
  position: relative;
  z-index: 9999;
}
.is-view .l-gnav .gnav-botton .gnav-line:nth-of-type(1) {
  background: var(--base-color02);
  -webkit-transform-origin: 0px 0px;
  -ms-transform-origin: 0px 0px;
  transform-origin: 0px 0px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -4px;
  left: 2px;
  opacity: 0;
  width: 31px;
  animation: is-gnav-line01 0.8s;
  animation-fill-mode: both;
}
@keyframes is-gnav-line01 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.is-view .l-gnav .gnav-botton .gnav-line:nth-of-type(2) {
  opacity: 0;
}
.is-view .l-gnav .gnav-botton .gnav-line:nth-of-type(3) {
  -webkit-transform-origin: 0px 0px;
  -ms-transform-origin: 0px 0px;
  transform-origin: 0px 0px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 16px;
  left: 1px;
  width: 31px;
  background: var(--base-color02);
  opacity: 0;
  animation: is-gnav-line02 0.8s;
  animation-fill-mode: both;
}
@keyframes is-gnav-line02 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
