  .is-view .l-gnav .gnav-menu .gnav-inner:nth-of-type(2), .is-view .l-gnav .gnav-menu .gnav-inner:nth-of-type(3), .is-view .l-gnav .gnav-menu .gnav-inner:nth-of-type(4), .is-view .l-gnav .gnav-menu .gnav-inner:nth-of-type(5), .is-view .l-gnav .gnav-menu .gnav-inner:nth-of-type(6) {
